.so-panels-dialog-wrapper {
  .ls-widget {
    margin: 10px 0;

    label {
      margin-bottom: 5px;
      display: block;
      margin-left: 1px;
      font-weight: bold;
    }

    img {
      margin-top: 3px;
    }

    textarea {
      width: 100%;
      font-family: "Open Sans", Helvetica, Arial, sans-serif;
    }
  }

  .ls-widget-heading {
    &:hover {
      cursor: pointer;
    }
    span {
      font-size: x-large;
    }
  }

  .ls-widget-group {
    display: none;
    padding: 10px;

    .ls-widget:first-child {
      margin-top: 0;
    }

    h4 {
      margin: 0;
      color:brown;
    }

    h3 {
      margin: 0;
      color: #ffb900;
    }
  }

  .group-open {
    display: block;
    background-color: #f1f1f1;
  }
}



